.modal {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 250px;
  background: #252526 0% 0% no-repeat padding-box;
  box-shadow: 0px 3px 30px #0000007a;
  border: 1px solid #252526;
  opacity: 1;

  padding: 50px 0;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;

  & > a {
    text-decoration: none;
  }
}
